import type { AxiosResponse } from 'axios'
import { request } from '@/utils/request'
import type { IResponse, IListResp } from '@/types/response'
import type { Project, CreateProjectParams, UpdateProjectParams, ProjectListParams } from '@/types/project'

const api = {
  list: '/projects/list',
  create: '/projects/create',
  updateName: '/projects/update_name',
  delete: '/projects/delete',
  restore: '/projects/restore',
  pin: '/projects/pin',
  unpin: '/projects/unpin',
  update_image_type: '/projects/update_image_type',
  add_member: '/projects/add_user',
  remove_member: '/projects/remove_user',
  get_members: '/projects/get_users',
  sampleCount: '/projects/sample_count',
  update_timestamp: '/projects/update_timestamp'  // 更新项目的最后访问时间
}

export function getProjects(params: ProjectListParams) {
  return request.get<IResponse<IListResp<Project>>>(api.list, { params })
}

export function createProject(data: CreateProjectParams) {
  return request.post<IResponse<Project>>(api.create, data)
}

export interface UpdateNameParams {
  project_id: number
  name: string
}

export function updateName(data: UpdateNameParams) {
  return request.patch<IResponse<Project>>(api.updateName, data)
}

export function updateProject(id: number, data: UpdateProjectParams) {
  return request.put<IResponse<Project>>(api.update(id), data)
}

export interface DeleteProjectParams {
  project_id: number
  is_recycle: number
}

export function deleteProject(data: DeleteProjectParams) {
  return request.post<IResponse<null>>(api.delete, data)
}

export interface RestoreProjectParams {
  project_id: number
}

export function restoreProject(data: RestoreProjectParams) {
  return request.post<IResponse<null>>(api.restore, data)
}

export interface PinProjectParams {
  project_id: number
}

export function pinProject(data: PinProjectParams) {
  return request.post<IResponse<null>>(api.pin, data)
}

export interface UnpinProjectParams {
  project_id: number
}

export function unpinProject(data: UnpinProjectParams) {
  return request.post<IResponse<null>>(api.unpin, data)
}

export interface UpdateImageTypeParams {
  project_id: number
  image_type: number
}

export function updateImageType(data: UpdateImageTypeParams) {
  return request.post<IResponse<null>>(api.update_image_type, data)
}

/**
 * 添加项目成员
 * @param data 添加成员参数
 * @returns 添加结果
 */
export interface AddMemberParams {
  project_id: number
  user_id: number
}

export function addProjectMember(data: AddMemberParams) {
  return request.post<IResponse<null>>(api.add_member, data)
}

/**
 * 移除项目成员
 * @param data 移除成员参数
 * @returns 移除结果
 */
export interface RemoveMemberParams {
  project_id: number
  user_id: number
}

export function removeProjectMember(data: RemoveMemberParams) {
  return request.post<IResponse<null>>(api.remove_member, data)
}

/**
 * 获取项目成员列表
 * @param projectId 项目 ID
 * @returns 项目成员列表
 */
export interface ProjectMember {
  user_id: number
  username: string
  permission: number
}

export interface ProjectMembersResponse {
  total: number
  users: ProjectMember[]
}

export function getProjectMembers(projectId: number) {
  return request.get<IResponse<ProjectMembersResponse>>(api.get_members, { 
    params: { project_id: projectId }
  })
}

/**
 * 获取项目样本数量
 * @param projectId 项目 ID
 * @returns 样本数量
 */

export interface SampleCount {
  project_id: number
}

export interface SampleCountResponse {
  sample_count: number
  project_id: number
}

export function getSampleCount(data: SampleCount) {
  return request.get<IResponse<SampleCountResponse>>(api.sampleCount, {
    params: data
  })
}

/**
 * 更新项目最后访问时间
 * @param data 更新时间参数
 * @returns 更新结果
 */
export interface UpdateTimestampParams {
  project_id: number
}

export function updateTimestamp(data: UpdateTimestampParams) {
  return request.post<IResponse<null>>(api.update_timestamp, data)
}
